<template>
  <div id="app">
    <!-- header -->
    <mt-header :title="headTitle" fixed>
      <span @click="goBack" slot="left" v-show="flag">
        <mt-button icon="back"></mt-button>
      </span>
    </mt-header>
    <!-- content -->
    <router-view class="content"></router-view>
    <!--  -->
    <myTabbar :selected="selected" :icons="icons" :iconsSelected="iconsSelected"></myTabbar>
  </div>
</template>
<script>
import myTabbar from "@/components/tabbar.vue";
export default {
  components: {
    myTabbar
  },
  data() {
    return {
      selected: "home", //tabbarselected
      icons: [
        //
        require("@/assets/img/icon/home.png"),
        require("@/assets/img/icon/classify.png"),
        require("@/assets/img/icon/cart.png"),
        require("@/assets/img/icon/my.png")
      ],
      iconsSelected: [
        //
        require("@/assets/img/icon/home-selected.png"),
        require("@/assets/img/icon/classify-selected.png"),
        require("@/assets/img/icon/cart-selected.png"),
        require("@/assets/img/icon/my-selected.png")
      ],
      headTitle: "",
      flag: false //
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  },
  watch: {
    "$route.path": function(now, old) {
      switch (now) {
        case "/home":
          this.flag = false;
          this.headTitle = "";
          break;
        case "/classify":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/cart":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/my":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/todayGame":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/rank":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/news":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/star":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/shop":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/videos":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/todayGame/matchinfo":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/news/newinfo":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/videos/videoplay":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/productdetail":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/pay":
          this.flag = true;
          this.headTitle = "";
          break;
        case "/login":
          this.flag = true;
          this.headTitle = "";
          break;
      }
    }
  }
};
</script>

